"use client";

import React from "react";

interface ProductListProps {
  products: {
    wsCode: number;
    name: string;
    salesPrice: number;
    mrp: number;
    packageSize: number;
    category: string;
    enabled: boolean;
  }[];
}

const ProductList: React.FC<ProductListProps> = ({ products }) => {
  return (
    <div className="mt-8">
      <h2 className="text-2xl font-bold mb-4">Existing Products</h2>
      <table className="table-auto w-full border-collapse border border-gray-200 shadow-md rounded-lg">
        <thead>
          <tr className="bg-blue-500 text-white">
            <th className="py-2 px-4">WS Code</th>
            <th className="py-2 px-4">Name</th>
            <th className="py-2 px-4">Sales Price</th>
            <th className="py-2 px-4">MRP</th>
            <th className="py-2 px-4">Package Size</th>
            <th className="py-2 px-4">Category</th>
            <th className="py-2 px-4">Status</th>
          </tr>
        </thead>
        <tbody>
          {products?.map((product) => (
            <tr key={product.wsCode} className="hover:bg-gray-100">
              <td className="py-2 px-4 text-center">{product.wsCode}</td>
              <td className="py-2 px-4 text-center">{product.name}</td>
              <td className="py-2 px-4 text-center">{product.salesPrice}</td>
              <td className="py-2 px-4 text-center">{product.mrp}</td>
              <td className="py-2 px-4 text-center">{product.packageSize}</td>
              <td className="py-2 px-4 text-center">{product.category}</td>
              <td className="py-2 px-4 text-center">
                <span
                  className={`px-2 py-1 rounded-full ${
                    product.enabled ? "bg-green-500 text-white" : "bg-red-500 text-white"
                  }`}
                >
                  {product.enabled ? "Active" : "Inactive"}
                </span>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default ProductList;
